import { getExpressionIds } from "@rp/react/tanstack-api/user-data-queries"
import { useQueryContext } from "@rp/react/web/common/providers"
import { AutosuggestInput } from "@rp/react/web/common/search-autosuggest-input"
import { isCqsQueryNotEmpty } from "@rp/react/web/cqs-utils"
import { useHandleCqsChange } from "../../hooks/useHandleCqsChange"

const ResultsSuggestionBar = () => {
  const handleCQSChange = useHandleCqsChange()
  const {
    savedQuery: {
      query: { expression },
    },
  } = useQueryContext()

  const selectedItems: Array<string> = expression
    ? getExpressionIds(expression, { format: "suggestionId", includeMacros: true })
    : []

  return (
    <AutosuggestInput
      placeholder={
        isCqsQueryNotEmpty({ expression })
          ? "Add more search terms to your query"
          : "Search for entities, topics, watchlists or keywords"
      }
      onChange={handleCQSChange}
      excludeQueryTypes={["savedSearch"]}
      selectedItems={[...selectedItems]}
      variant="results"
    />
  )
}

export { ResultsSuggestionBar }
